* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.header{
  display: flex;
  justify-content: center;
  align-items: center;
}

body {
  font-family: "Segoe UI", Arial, sans-serif;
  line-height: 1.4;
  font-family: bold;
  color: #fef4f4;
  text-transform: uppercase; 
  
  background:#000000d1;
  letter-spacing: 2px;

  
}


h1 {
  font-size: 40px;
  
  margin-bottom: 27px;
}
.meme-container {
  display: flex;
  justify-content: center;
  align-items: center;
 

}
form {
  background: #ad1457;
  text-align: center;
  padding-top: 20px;
  width:480px;
  height:350px;
  border-bottom-left-radius:10px ;
  border-top-left-radius:10px ;
}
input,
button {
  width:70%;
  height:50px;
  margin-bottom: 5px;
  border: none;
  outline: none;
}
input {
  padding: 5px 8px;
  border-radius: 10px;
  font-size: 20px;
  letter-spacing: 2px;
}
button {
  border-radius: 10px;
  cursor: pointer;
  text-transform: uppercase;
  color: #ad1457;
  font-weight: bold;
  margin-top: 50px;
  font-size: 20px;

}
button:hover{
  background-color: darkcyan;
  color: aliceblue;
  transition: .6s ease-in-out ;
  
}
.meme {
  position: relative;
}
.meme > img {
  width:auto;
  max-height:350px ;
  background: cover;
  margin-bottom: -5px;
  background-repeat: no-repeat;
  border-bottom-right-radius:10px ;
  border-top-right-radius:10px ;
}



.meme > h2{
  position: absolute;
  max-width: 36em;
  font-size: 36px;
  text-align: center;
  left: 49%;
  transform: translateX(-50%);
  margin: 0.3em 0;
  padding: 0;
  font-family: impact, sans-serif;
  text-transform: uppercase;
  color: white;
  letter-spacing: 2px;
  text-shadow: 2px 2px 0 #000, -2px -2px 0 #000, 2px -2px 0 #000,
    -2px 2px 0 #000, 0 2px 0 #000, 2px 0 0 #000, 0 -2px 0 #000, -2px 0 0 #000,
    2px 2px 5px #000;
}
.meme > .top {
  top: 0.1em;
}
.meme > .bottom {
  bottom: 0.1em;
}

/* On screens that are 768px or less */
@media screen and (max-width: 768px) {
  h1 {
    font-size: 30px;
    margin-bottom: 14px;
  }
  .meme-container {
    flex-direction: column;
  }
  .meme > h2 {
    font-size: 30px;
  }
}